.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-content {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 450px;
  width: 90%;
  animation: slideUp 0.3s ease;
  overflow: hidden;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 2px solid #e2e8f0;
}

.modal-header h3 {
  margin: 0;
  font-size: 1.25rem;
  color: #2d3748;
  font-weight: 700;
}

.modal-close {
  background: none;
  border: none;
  font-size: 2rem;
  color: #718096;
  cursor: pointer;
  line-height: 1;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: #f7fafc;
  color: #2d3748;
}

.modal-body {
  padding: 24px;
}

.modal-body p {
  margin: 0;
  color: #4a5568;
  line-height: 1.6;
  font-size: 0.95rem;
}

.modal-footer {
  display: flex;
  gap: 12px;
  padding: 20px 24px;
  background: #f7fafc;
  justify-content: flex-end;
}

.modal-btn {
  padding: 10px 24px;
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 2px solid transparent;
}

.modal-btn-cancel {
  background: white;
  color: #4a5568;
  border-color: #cbd5e1;
}

.modal-btn-cancel:hover {
  background: #f7fafc;
  border-color: #94a3b8;
}

.modal-btn-confirm {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
}

.modal-btn-confirm:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.modal-btn-confirm.danger {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
}

.modal-btn-confirm.danger:hover {
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.4);
}

.modal-btn:active {
  transform: translateY(0);
}

/* Dark mode */
.dark-mode .modal-overlay {
  background: rgba(0, 0, 0, 0.8);
}

.dark-mode .modal-content {
  background: #1a1a2e;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);
}

.dark-mode .modal-header {
  border-bottom-color: #3a3a4e;
}

.dark-mode .modal-header h3 {
  color: #e2e8f0;
}

.dark-mode .modal-close {
  color: #94a3b8;
}

.dark-mode .modal-close:hover {
  background: #2d3748;
  color: #e2e8f0;
}

.dark-mode .modal-body p {
  color: #cbd5e1;
}

.dark-mode .modal-footer {
  background: #0f0f1e;
}

.dark-mode .modal-btn-cancel {
  background: #2d3748;
  color: #cbd5e1;
  border-color: #4a5568;
}

.dark-mode .modal-btn-cancel:hover {
  background: #374151;
  border-color: #6b7280;
}
