/* ========================================
   PUBLIC PROFILE PAGE - ROOT CONTAINER
   ======================================== */
.public-profile-root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px 16px 24px 16px;
  gap: 0;
  font-family: "Segoe UI", Roboto, Arial, sans-serif;
  position: relative;
  transition: background 0.3s ease;
}

/* ========================================
   PUBLIC PROFILE CARD
   ======================================== */
.public-profile-card {
  width: 100%;
  max-width: 1000px;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  padding: 24px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* ========================================
   LOADING & ERROR STATES
   ======================================== */
.loading-container,
.error-container {
  max-width: 500px;
  margin: 100px auto;
  background: #fff;
  border-radius: 20px;
  padding: 60px 40px;
  text-align: center;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.loading-spinner {
  width: 50px;
  height: 50px;
  margin: 0 auto 20px;
  border: 4px solid #e2e8f0;
  border-top-color: #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.error-icon {
  font-size: 60px;
  margin-bottom: 20px;
}

.error-container h2 {
  color: #e53e3e;
  margin-bottom: 20px;
}

.btn-back {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  border: none;
  padding: 12px 24px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.btn-back:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}

/* ========================================
   PROFILE HEADER
   ======================================== */
.profile-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  border-bottom: 2px solid #f1f5f9;
  padding-bottom: 16px;
}

.profile-header-left {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;
  flex: 1;
}

.profile-avatar {
  display: flex;
  justify-content: center;
}

.profile-avatar img,
.avatar-fallback {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #667eea;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
  transition: all 0.3s ease;
}

.avatar-fallback {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  font-weight: 700;
}

.profile-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 6px;
  flex: 1;
}

.profile-info h2 {
  margin: 0;
  font-size: 1.75rem;
  font-weight: 800;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.profile-sub {
  font-size: 0.9rem;
  color: #718096;
  margin: 0;
  font-weight: 500;
}

/* ========================================
   PROFILE BODY
   ======================================== */
.profile-body {
  display: flex;
  flex-direction: row;
  gap: 24px;
  align-items: flex-start;
  justify-content: center;
}

.profile-left {
  flex: 1;
  min-width: 0;
  max-width: 800px;
  margin: 0 auto;
}

.profile-left h3 {
  margin: 0 0 10px 0;
  font-size: 1.15rem;
  font-weight: 700;
  color: #667eea;
}

/* ========================================
   BIO SECTION
   ======================================== */
.bio-section {
  margin-bottom: 16px;
}

.bio-display {
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
  padding: 18px;
  border-radius: 12px;
  border: 2px solid #e2e8f0;
  transition: all 0.3s ease;
}

.bio-display:hover {
  border-color: #cbd5e1;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.1);
}

.profile-bio {
  margin: 0;
  color: #2d3748;
  line-height: 1.6;
  font-size: 0.95rem;
  white-space: pre-wrap;
  word-wrap: break-word;
  min-height: 30px;
}

/* ========================================
   PROFILE DETAILS
   ======================================== */
.profile-details {
  list-style: none;
  padding: 0;
  margin: 12px 0 0 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px 20px;
}

.profile-details li {
  padding: 14px;
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
  border-radius: 10px;
  border: 2px solid #e2e8f0;
  transition: all 0.3s ease;
}

.profile-details li:hover {
  border-color: #cbd5e1;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.1);
  transform: translateY(-1px);
}

.editable-row {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}

.editable-row strong {
  font-weight: 700;
  color: #667eea;
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.8px;
}

.editable-value {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  justify-content: space-between;
}

.field-value {
  flex: 1;
  min-width: 0;
  word-break: break-word;
  font-size: 1rem;
  color: #2d3748;
  text-align: left;
}

/* ========================================
   MESSAGE SECTION
   ======================================== */
.message-section {
  margin-top: 24px;
}

.message-section h3 {
  margin: 0 0 10px 0;
  font-size: 1.15rem;
  font-weight: 700;
  color: #667eea;
}

.message-hint {
  color: #718096;
  margin-bottom: 16px;
  font-size: 0.95rem;
  line-height: 1.5;
}

.inbox-section {
  margin-top: 24px;
}

.inbox-section h3 {
  margin: 0 0 10px 0;
  font-size: 1.15rem;
  font-weight: 700;
  color: #667eea;
}

.message-count {
  font-size: 0.9rem;
  font-weight: 600;
  color: #718096;
  margin-left: 6px;
}

/* Message List Styles */
.messages-list {
  list-style: none;
  padding: 14px;
  margin: 0;
  max-height: 500px;
  overflow-y: auto;
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
  border-radius: 12px;
  border: 2px solid #e2e8f0;
}

.messages-list::-webkit-scrollbar {
  width: 10px;
}

.messages-list::-webkit-scrollbar-track {
  background: #edf2f7;
  border-radius: 10px;
}

.messages-list::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 10px;
}

.messages-list::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #764ba2 0%, #667eea 100%);
}

.message-item {
  background: #fff;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  padding: 14px;
  margin-bottom: 10px;
  transition: all 0.3s ease;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.message-item:hover {
  border-color: #667eea;
  box-shadow: 0 3px 10px rgba(102, 126, 234, 0.2);
  transform: translateY(-1px);
}

.message-item:last-child {
  margin-bottom: 0;
}

.msg-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.msg-sender {
  font-weight: 700;
  font-size: 0.7rem;
  color: #667eea;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  display: inline-block;
  padding: 4px 10px;
  background: rgba(102, 126, 234, 0.1);
  border-radius: 6px;
}

.msg-content {
  margin-top: 12px;
  color: #2d3748;
  line-height: 1.6;
  font-size: 0.95rem;
  word-wrap: break-word;
  word-break: break-word;
  overflow-wrap: break-word;
  white-space: pre-wrap;
}

.msg-time {
  margin-top: 10px;
  color: #a0aec0;
  font-size: 0.75rem;
  font-weight: 500;
}

.no-messages {
  color: #475569;
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
  padding: 40px 20px;
  border-radius: 14px;
  border: 2px solid #e2e8f0;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
}

.send-error {
  background: #fed7d7;
  color: #c53030;
  padding: 10px 14px;
  border-radius: 8px;
  margin-bottom: 12px;
  font-size: 14px;
  border: 1px solid #fc8181;
}

.send-success {
  background: #c6f6d5;
  color: #22543d;
  padding: 12px 16px;
  border-radius: 10px;
  font-size: 0.95rem;
  text-align: center;
  border: 1px solid #68d391;
  font-weight: 600;
  margin-bottom: 12px;
  animation: slideInDown 0.3s ease-out;
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.message-textarea {
  width: 100%;
  min-height: 120px;
  padding: 14px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 15px;
  font-family: inherit;
  line-height: 1.7;
  resize: vertical;
  box-sizing: border-box;
  transition: all 0.3s ease;
}

.message-textarea:focus {
  border-color: #667eea;
  outline: none;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.message-textarea:disabled {
  background: #f7fafc;
  cursor: not-allowed;
  opacity: 0.7;
}

.message-textarea::placeholder {
  color: #a0aec0;
}

.char-count {
  text-align: right;
  font-size: 12px;
  color: #718096;
  margin-top: 6px;
  margin-bottom: 8px;
}

.btn-send {
  width: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  color: #fff;
  padding: 12px 32px;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 700;
  font-size: 1rem;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn-send:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}

.btn-send:active:not(:disabled) {
  transform: translateY(0);
}

.btn-send:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.btn-send:focus {
  outline: 3px solid rgba(102, 126, 234, 0.5);
  outline-offset: 2px;
}

/* ========================================
   PROFILE FOOTER
   ======================================== */
.profile-footer {
  display: flex;
  justify-content: center;
  padding-top: 20px;
  border-top: 2px solid #f1f5f9;
}

.login-prompt {
  margin: 0;
  text-align: center;
  color: #4a5568;
  font-size: 0.95rem;
}

.link {
  color: #667eea;
  font-weight: 700;
  text-decoration: none;
  transition: all 0.2s;
}

.link:hover {
  color: #764ba2;
  text-decoration: underline;
}

/* ========================================
   RESPONSIVE DESIGN
   ======================================== */
@media (max-width: 800px) {
  .public-profile-card {
    max-width: 98vw;
    padding: 16px 12px;
  }

  .profile-header {
    flex-direction: column;
    align-items: center;
  }

  .profile-header-left {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .profile-info {
    align-items: center;
  }

  .profile-body {
    flex-direction: column;
  }

  .profile-left {
    max-width: 100%;
  }

  .profile-details {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .profile-info h2 {
    font-size: 1.4rem;
  }

  .profile-avatar img,
  .avatar-fallback {
    width: 70px;
    height: 70px;
    font-size: 28px;
  }
}

/* ========================================
   DARK MODE STYLES
   ======================================== */
.public-profile-root.dark-mode {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
}

.dark-mode .public-profile-card {
  background: #1e1e2e;
  color: #e2e8f0;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.6);
}

.dark-mode .profile-header {
  border-bottom-color: #2d3748;
}

.dark-mode .profile-sub {
  color: #a0aec0;
}

.dark-mode .loading-container,
.dark-mode .error-container {
  background: #1e1e2e;
  color: #e2e8f0;
}

.dark-mode .loading-spinner {
  border-color: #2d3748;
  border-top-color: #667eea;
}

.dark-mode .profile-left h3,
.dark-mode .message-section h3 {
  color: #8b95ea;
}

.dark-mode .message-hint {
  color: #a0aec0;
}

.dark-mode .inbox-section h3 {
  color: #8b95ea;
}

.dark-mode .message-count {
  color: #94a3b8;
}

.dark-mode .messages-list {
  background: linear-gradient(135deg, #2a2a3e 0%, #2d3748 100%);
  border-color: #3a3a4e;
}

.dark-mode .messages-list::-webkit-scrollbar-track {
  background: #2d3748;
}

.dark-mode .message-item {
  background: #1e1e2e;
  border-color: #3a3a4e;
}

.dark-mode .message-item:hover {
  border-color: #667eea;
  box-shadow: 0 3px 10px rgba(102, 126, 234, 0.3);
}

.dark-mode .msg-sender {
  color: #8b95ea;
  background: rgba(139, 149, 234, 0.15);
}

.dark-mode .msg-content {
  color: #cbd5e1;
}

.dark-mode .msg-time {
  color: #718096;
}

.dark-mode .no-messages {
  background: linear-gradient(135deg, #2a2a3e 0%, #2d3748 100%);
  border-color: #3a3a4e;
  color: #94a3b8;
}

.dark-mode .bio-display {
  background: linear-gradient(135deg, #2a2a3e 0%, #2d3748 100%);
  border-color: #3a3a4e;
}

.dark-mode .profile-bio {
  color: #cbd5e1;
}

.dark-mode .profile-details li {
  background: linear-gradient(135deg, #2a2a3e 0%, #2d3748 100%);
  border-color: #3a3a4e;
}

.dark-mode .profile-details li:hover {
  border-color: #4a5568;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.2);
}

.dark-mode .editable-row strong {
  color: #8b95ea;
}

.dark-mode .field-value {
  color: #e2e8f0;
}

.dark-mode .message-textarea {
  background: #2a2a3e;
  border-color: #3a3a4e;
  color: #e2e8f0;
}

.dark-mode .message-textarea:focus {
  background: #2a2a3e;
  border-color: #667eea;
}

.dark-mode .message-textarea:disabled {
  background: #1e1e2e;
}

.dark-mode .message-textarea::placeholder {
  color: #4a5568;
}

.dark-mode .char-count {
  color: #a0aec0;
}

.dark-mode .send-error {
  background: #742a2a;
  color: #fed7d7;
}

.dark-mode .send-success {
  background: #22543d;
  color: #c6f6d5;
  border-color: #68d391;
}

.dark-mode .profile-footer {
  border-top-color: #2d3748;
}

.dark-mode .login-prompt {
  color: #cbd5e1;
}
