.reset-password-root {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
  position: relative;
}

.reset-password-root.dark-mode {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
}

.reset-password-card {
  background: white;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 420px;
}

.dark-mode .reset-password-card {
  background: #2d2d44;
  color: #e0e0e0;
}

.reset-password-card h2 {
  margin: 0 0 12px 0;
  text-align: center;
  color: #333;
  font-size: 28px;
}

.dark-mode .reset-password-card h2 {
  color: #e0e0e0;
}

.reset-password-description {
  text-align: center;
  color: #666;
  margin: 0 0 24px 0;
  font-size: 14px;
  line-height: 1.5;
}

.dark-mode .reset-password-description {
  color: #b0b0b0;
}

.reset-password-error {
  background-color: #fee;
  color: #c33;
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 16px;
  font-size: 14px;
  border: 1px solid #fcc;
}

.dark-mode .reset-password-error {
  background-color: #4a2020;
  color: #ff6b6b;
  border-color: #6a3030;
}

.reset-password-success {
  background-color: #efe;
  color: #2a7d2e;
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 16px;
  font-size: 14px;
  border: 1px solid #cec;
  text-align: center;
}

.dark-mode .reset-password-success {
  background-color: #1e4a1e;
  color: #6fbf73;
  border-color: #2e5a2e;
}

.reset-password-card form {
  display: flex;
  flex-direction: column;
}

.reset-password-label {
  font-weight: 600;
  margin-bottom: 6px;
  color: #555;
  font-size: 14px;
}

.dark-mode .reset-password-label {
  color: #c0c0c0;
}

.reset-password-input {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 15px;
  margin-bottom: 16px;
  transition: border-color 0.2s;
}

.reset-password-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.dark-mode .reset-password-input {
  background: #3a3a52;
  border-color: #4a4a62;
  color: #e0e0e0;
}

.dark-mode .reset-password-input:focus {
  border-color: #8b9aee;
  box-shadow: 0 0 0 3px rgba(139, 154, 238, 0.1);
}

.reset-password-input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.reset-password-input[readonly] {
  background-color: #f5f5f5;
}

.dark-mode .reset-password-input[readonly] {
  background-color: #2a2a3a;
}

.reset-password-button {
  padding: 12px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  margin-top: 8px;
  margin-bottom: 20px;
}

.reset-password-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.reset-password-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.dark-mode .reset-password-button {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a3f92 100%);
}

.reset-password-footer {
  text-align: center;
  margin-top: 8px;
}

.reset-password-footer small {
  color: #666;
}

.dark-mode .reset-password-footer small {
  color: #b0b0b0;
}

.reset-password-link {
  color: #667eea;
  text-decoration: none;
  font-weight: 600;
}

.reset-password-link:hover {
  text-decoration: underline;
}

.dark-mode .reset-password-link {
  color: #8b9aee;
}

@media (max-width: 480px) {
  .reset-password-card {
    padding: 30px 20px;
  }

  .reset-password-card h2 {
    font-size: 24px;
  }
}
